.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 100%;
    grid-template-areas: "h h" "n c";
    grid-template-rows: 70px 13fr;
    grid-template-columns: 1fr 10fr;
}

.app-wrapper-content {
    border-radius: 15px;
    min-height: 100vh;
    height: 100%;
    max-width: 100%;
    grid-area: c;
    background-color: #bbbbbb;
    color: rgb(13, 13, 13);
}

@media screen and (max-width: 750px) {
    .app-wrapper {
        display: grid;
        width: 100%;
        grid-template-areas: "h h" "c c";
        grid-template-rows: 70px ;
        grid-template-columns: 1fr 10fr;
    }
}